//直接引用blog，无需使用Vuex
import blog from '@/api/blog'

export default {
  data() {
        return { //v-model模块 初始数据双向绑定，自动同步
            blogId:null,
            title: '',
            description: '',
            content: '',
            atIndex: false,
     }
    },
    
    created() {
      
      //如何获取blogId：在路由 router 中 path: '/edit/:blogId' 存放blog的id
      //使用params方式传递路由参数 方法：this.$route.params.name
        this.blogId = this.$route.params.blogId
        blog.getDetail({ blogId: this.blogId }).then(res => {
            this.title = res.data.title
            this.description = res.data.description
            this.content = res.data.content
            this.atIndex = res.data.atIndex
     })
    },
    //获取id 为 blogId 的博客详情（:blogId替换用户传递的ID）
    /*
      getDetail({ blogId }) {
        return request(URL.GET_DETAIL.replace(':blogId', blogId))
    }
    */

  methods: {  
    onEdit() { //侦听编辑
      blog.updateBlog({ blogId: this.blogId },{ title: this.title, content: this.content, description: this.description, atIndex: this.atIndex })
        .then(res => {
          this.$message.success(res.msg) //打印成功信息
          this.$router.push({ path: `/detail/${res.data.id}`})//路由跳转到博客详情页，id储存在返回的res字符段中
       })
       //修改博客 id 为:blogId 的博客
        /*
        updateBlog({ blogId }, { title, content, description, atIndex }) {
            return request(URL.UPDATE.replace(':blogId', blogId), 'PATCH', { title, content, description, atIndex })
        }
        */
       
     },
   }
}


